.Contenido
{
  width: 100%;
  height: 90vh;
  display: flex;
  flex-wrap: wrap;
}

/*Principal*/

.Cont-apartado
{
  display: flex;
  width: 33%;
  height: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  cursor:pointer;
}

.Cont-hover
{
  filter:brightness(0.3);
}

.Casas
{
  background-image: url("images/Casas2.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

.Spells
{
  background-image: url("images/Spells2.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

.Potions
{
  background-image: url("images/potions.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

/*Casas*/

.Cont-Casas
{

  width: 100%;
  display: flex;
  align-items: center;

}

.Cont-Casas-casa
{
  width: 50%;
  height: 100%;
  text-align: center;
  cursor:pointer;
}

.Gry
{
  background-image: url("images/gryffindor.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;

}

.Sly
{
  background-image: url("images/Slythering.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;

}

.Huff
{
  background-image: url("images/hufflepuff.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

.Raven
{
  background-image: url("images/ravenclaw.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
}

/*Casa*/

.Cont-Casa
{
  font-size: 50px;
  width: 100%;
}

.Cont-Casa-NombreCasa
{
  width: 100%;
  text-align: center;
  margin-top: 100px;
  margin-bottom: 10px;
  font-size: 5vw;
  font-family: 'Harry Potter', sans-serif;                                              
  
}

.Cont-Casa-CreadorCasa
{
  width: 100%;
  text-align: center;
  font-size: 2vw;
  font-family: 'Harry Potter', sans-serif;                                              

}

.Cont-Casa-Secundario
{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.Cont-Casa-Secundario-Fila1, .Cont-Casa-Secundario-Fila2, .Cont-Casa-Secundario-Fila3
{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
    align-items: center;
}

.Cont-Casa-ColoresCasa, .Cont-Casa-AnimalCasa, .Cont-Casa-JefesCasa
{
  width: 40%;
  text-align: center;
  margin-top: 200px;
}

.Cont-Casa-ElementoCasa, .Cont-Casa-FantasmaCasa, .Cont-Casa-SalaComunCasa, .Cont-Casa-Valores
{
  width: 30%;
  text-align: center;
  margin-top: 200px;
}

.Cont-casa-JefesCasa-Nomnre, .Cont-casa-Valores-valor
{
  width: 100%;
  text-align: center;
  margin-top: 15px;
}

.Cont-Casa-Gry
{
  height: 100%;
  background-image: url("images/gryffindor_fondo.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  color: #b7931a;
  text-shadow: 1px 1px 10px #1a1919;  

}

.Cont-Casa-Sly
{
  height: 100%;
  background-image: url("images/Slythering_fondo.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  color: #9e9e9e;
  text-shadow: 1px 1px 10px #1a1919;  

}

.Cont-Casa-Huff
{
  height: 100%;
  background-image: url("images/hufflepuff_fondo.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  color: #040607;
}

.Cont-Casa-Raven
{
  height: 100%;
  background-image: url("images/ravenclaw_fondo.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  color: #b58a2d;
  text-shadow: 1px 1px 10px #1a1919;  

}

/*Spells*/

.Cont-Spells
{
  width: 100%;
  background-image: url("images/spells_fondo.png");
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Cont-Spells-Cards
{
  width: 80%;
  height: 98%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Cont-Spells-SpellCard
{
  width: 300px;
  margin: 50px;
  height: fit-content;
  padding: 20px;
  background: rgba( 23, 8, 90, 0.75 );
  box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba( 255, 255, 255, 0.18 );

  text-align: center;
  font-weight: 900;
  font-size: 20px;
  cursor: pointer;
}
.Cont-Spells-SpellCard-Oculto
{
  display: none;
}
.Cont-Spells-SpellCard-Text
{
  margin: 15px;
}
.Cont-Spells-SpellCard-Text div
{
  font-weight: 100 !important;
}

.Spell-Tipo
{
  margin: 0px 10px 10px 10px !important;
}

.Cont-Spells-Filter
{
  width: 100%;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
}



/*Flecha Atras*/
.FlechaAnterior
{

  font-size: 100px;
  position: absolute;
  top: -13px;
  left: 30px;
  cursor:pointer;
  color:aliceblue;
}